/*
  Senicare 프로젝트 공통 CSS
*/
@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Moirai+One&display=swap');

@font-face {
  font-family: 'Pretendard';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

:root {

  /* 변수처럼 지정 가능 */
  --primary-color: rgba(0, 192, 122, 1);
  --primary-10-color: rgba(0, 192, 122, 0.1);
  --primary-20-color: rgba(0, 192, 122, 0.2);
  --error-color: rgba(255, 84, 64, 1);
  --gray-color: rgba(138, 146, 166, 1);
  --dark-gray-color: rgba(64, 64, 64, 1);
  --default-color: rgba(35, 45, 66, 1);
}

body {
  margin: 0px;
  font-family: 'Pretendard', sans-serif;
}

#main-wrapper {
  display: flex;
  /* 어떤 상황에서든 화면이 중앙으로 오게끔 고정 */
  justify-content: center;

  padding: 24px;
  background-color: rgba(238,238,238,0.5);
}

.message.error {
  color: var(--error-color);
}

.message.primary {
  color: var(--primary-color);
}

.button {
  border-radius: 6px;
  padding: 8px 24px;

  font-size: 16px;
  font-weight: 400;
  line-height: 175%;
  text-align: center;

  color: rgba(255, 255, 255, 1);
}

.button:hover {
  opacity: 0.9;
  cursor: pointer;
}

.button.primary {
  background-color: var(--primary-color);
}

.button.second {
  background-color: var(--primary-10-color);
  color: var(--primary-color);
}

.button.disable {
  background-color: var(--gray-color);
}

.button.error {
  background-color: var(--error-color);
}

.disable:hover {
  opacity: 1;
}

.full-width {
  width: 100%;
  box-sizing: border-box;
}

.link {
  align-self: center;

  font-size: 16px;
  font-weight: 400;
  line-height: 175%;

  color: rgba(113, 113, 113, 1);
}

.link:hover {
  cursor: pointer;
  font-weight: bolder;
}

.divider {
  width: 100%;
  border-bottom: 1px solid rgba(238, 238, 238, 1);
}

.sns-button {
  border-radius: 50%;

  width: 56px;
  height: 56px;

  background-size: 100% 100%;
}

.sns-button:hover {
  opacity: 0.8;
  cursor: pointer;
}

.sns-button.md {
  width: 40px;
  height: 40px;
}

.sns-button.kakao {
  background-image: url(/public/images/kakao-button.png);
}

.sns-button.naver {
  background-image: url(/public/images/naver-button.png);
}

.icon-button {
  width: 24px;
  height: 24px;

  background-size: 100% 100%;

  cursor:pointer;
}

.icon-button.edit {
  background-image: url(/public/images/edit-icon.png);
}
.icon-button.trash {
  background-image: url(/public/images/trash-icon.png);
}
